import Vue from 'vue'
import Vuex from 'vuex'
import {getJiDuDate} from './assets/js/core/weapon'
import apiService from './assets/js/api'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    dateStrInit: '', // getYesterDay() dateFormatter('yyyyMMdd', new Date()), // 
    dateStr: '', // dateFormatter('yyyyMMdd', new Date()), // 
    showIcon: true,
    showLeft: false,
    showHeader: true,
    showBottom: true,
    showError: false,
    showMask: false,
    headerTitle: '',
    authList: [
      {zgsName: '', dpName: ''},
      {zgsName: '', dpName: ''}
    ],
    dwList: [
      {id: '1', name: ''},
      {id: '2', name: ''},
      {id: '3', name: ''}
    ], // ,
    selectedDwName: '', // 
    dpList: [
      {id: '1', name: ''},
      {id: '2', name: ''}
    ],
    selectedDpName: '',
    isChangeUserType: false,
    selectedMultiShops: [],
    userIdentity: '', // 1 2 3 
    userIdentityInit: '' // 
  },
  getters: {
    dateStrM (state) {
      return state.dateStr.substring(0, 6)
    },
    dateStrJ (state) {
      return getJiDuDate(state.dateStr)
    }
  },
  actions: {
    async updateUserOption ({commit, state, dispatch}, payload) {
      const {__statusCode} = await apiService.getDataFromInterface(apiService.updateUserOption, payload)
      if (__statusCode !== '1') {
        // alert('~')
      }
    }
  },
  mutations: {
    updateDateStrInit (state, flag) {
      state.dateStrInit = flag
    },
    updateDateStr (state, flag) {
      state.dateStr = flag
    },
    updateShowIcon (state, flag) {
      state.showIcon = flag
    },
    updateShowLeft (state, flag) {
      state.showLeft = flag
    },
    updateShowHeader (state, flag) {
      state.showHeader = flag
    },
    updateShowBottom (state, flag) {
      state.showBottom = flag
    },
    updateShowError (state, flag) {
      state.showError = flag
    },
    updateHeaderTitle (state, str) {
      state.headerTitle = str
    },
    updateShowMask (state, payload) {
      state.showMask = payload
    },
    updateDwList (state, payload) {
      state.dwList = payload
      if (payload.length > 0) {
        state.selectedDwName = payload[0].name
      }
    },
    updateSelectedDwName (state, payload) {
      state.selectedDwName = payload
    },
    updateDpList (state, payload) {
      state.dpList = payload
      if (payload.length > 0) {
        state.selectedDpName = payload[0].name
      }
    },
    updateSelectedDpName (state, payload) {
      state.selectedDpName = payload
    },
    updateIsChangeUserType (state, payload) {
      state.isChangeUserType = payload
    },
    updateUserIdentity (state, payload) {
      state.userIdentity = payload
    },
    updateUserIdentityInit (state, payload) {
      state.userIdentityInit = payload
    },
    updateAuthList (state, payload) {
      state.authList = payload
    },
    updateSelectedMultiShops (state, payload) {
      state.selectedMultiShops = payload
    }
  }
})
