/**
 * @description 
 */

const LIB = {}
const DESIGN_WIDTH = process.env.DESIGN_WIDTH || 750
const DESIGN_HEIGHT = process.env.DESIGN_HEIGHT || 1334

let rem2px
let px2rem
let vh2px
let px2vh
let winHeight = 0
let winWidth = 0
let dpr = 1

// 
if (window.innerHeight) {
  winHeight = window.innerHeight
} else if ((document.body) && (document.body.clientHeight)) {
  winHeight = document.body.clientHeight
}

try {
  const doc = window.document // 
  const docEl = doc.documentElement // 
  let metaEl = doc.querySelector('meta[name="viewport"]') // viewport 
  dpr = window.devicePixelRatio || 1 // dpr
  const scale = 1 / dpr // viewport 
  let tid // 

  /* viewport  */
  if (!metaEl) {
    metaEl = doc.createElement('meta')
    metaEl.setAttribute('name', 'viewport')
    if (docEl.firstElementChild) {
      docEl.firstElementChild.appendChild(metaEl)
    } else {
      let wrap = doc.createElement('div')
      wrap.appendChild(metaEl)
      doc.write(wrap.innerHTML)
    }
  }

  // viewport
  metaEl.setAttribute('content', 'width=device-width, initial-scale=' + scale + ',maximum-scale=' + scale + ', minimum-scale=' + scale + ',user-scalable=no')
  // data-dprcss hack
  docEl.setAttribute('data-dpr', dpr)

  // 
  const refreshRem = () => {
    winWidth = docEl.getBoundingClientRect().width
    const rem = winWidth / 10
    docEl.style.fontSize = rem + 'px'
    if (window.innerHeight) {
      winHeight = window.innerHeight
    } else if ((document.body) && (document.body.clientHeight)) {
      winHeight = document.body.clientHeight
    }
    LIB.rem = rem
    LIB.winHeight = winHeight
    LIB.winWidth = winWidth
  }
  // 
  refreshRem()

  // resize
  window.addEventListener('resize', () => {
    window.clearTimeout(tid)
    tid = window.setTimeout(refreshRem, 300)
  })

  // 
  window.addEventListener('pageshow', (e) => {
    if (e.persisted) { // 
      window.clearTimeout(tid)
      tid = window.setTimeout(refreshRem, 300)
    }
  })

  /* body12px */
  if (doc.readyState === 'complete') {
    doc.body.style.fontSize = 12 * dpr + 'px'
  } else {
    doc.addEventListener('DOMContentLoaded', function (e) {
      doc.body.style.fontSize = 12 * dpr + 'px'
    }, false)
  }

  /* rempx */
  rem2px = LIB.rem2px = d => {
    if (isNaN(d)) {
      return d
    }
    return parseFloat(d) * LIB.rem
  }

  /* pxrem */
  px2rem = LIB.px2rem = d => {
    if (isNaN(d)) {
      return d
    }
    return parseFloat(d) / DESIGN_WIDTH * 10
  }

  /* vhpx */
  vh2px = LIB.vh2px = d => {
    if (isNaN(d)) {
      return d
    }
    return parseFloat(d) / 100 * winHeight
  }

  /* pxvh */
  px2vh = LIB.px2vh = d => {
    if (isNaN(d)) {
      return d
    }
    return parseFloat(d) / DESIGN_HEIGHT * 100
  }
} catch (error) {
  console.error('', error)
}

export default LIB
export { rem2px, px2rem, vh2px, px2vh, winWidth, winHeight }
